<script>
export default {
  data() {
    return {
      active: this.$route.params.id,
      search: "",
    };
  },
  methods: {
    goPath(name) {
      this.$router.push({ name });
    },
  },
};
</script>
<style lang="scss" scoped>
.orders_list {
  width: 100%;
  height: 100%;
  .head_nav {
    position: relative;
    &::after {
      position: absolute;
      box-sizing: border-box;
      content: " ";
      pointer-events: none;
      top: -50%;
      right: -50%;
      bottom: -50%;
      left: -50%;
      border-bottom: 1px solid #ebedf0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }
    .head_search {
      width: 100%;
      padding: 10px 18px;
      .van-search__content {
        border: solid 1px #7e3330;
        border-radius: 20px;
        background-color: #fff;
        /deep/ .van-cell {
          font-size: 14px;
          padding: 5px 8px 5px 0;
          .van-field__left-icon {
            height: 100%;
            i {
              display: flex;
              box-sizing: border-box;
              font-size: 20px;
            }
          }
        }
      }
    }
  }

  /deep/ .van-tabs {
    width: 100%;
    height: auto;
    pointer-events: none;
    .van-tabs__wrap {
      pointer-events: all;
      height: 40px;
    }
    .van-tabs__wrap::after {
      border-width: 0;
    }
    .van-tabs__content {
      pointer-events: all;
      padding: 20px;
      .van-card__desc {
        margin-top: 10px;
      }
      .van-cell {
        padding: 10px 16px 0px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        .van-cell__title {
          font-size: 16px;
        }
        .van-cell__left-icon {
          font-size: 16px;
        }
      }
      .van-cell:not(:last-child)::after {
        border-bottom: none;
      }
      .van-cell:active {
        background-color: #fff;
      }
      .cell_div {
        width: 100%;
        height: auto;
        .cell_info {
          border-radius: 0 0 5px 5px;
        }
      }
      .cell_info {
        padding: 5px 15px 15px;
        border-radius: 5px;
        margin-bottom: 15px;
        width: 100%;
        background-color: #fff;
        > .info_first {
          width: 100%;
          height: 95px;
          display: flex;
          .info_image {
            width: 125px;
            height: 100%;
            border-radius: 5px;
            overflow: hidden;
          }
          > div:last-child {
            flex-grow: 1;
            padding-left: 15px;
            height: 100%;
            display: flex;
            justify-content: space-between;
            align-items: baseline;
            > div:first-child {
              height: 100%;
              color: #000;
              > div:nth-of-type(2) {
                font-size: 13px;
                margin-top: 10px;
              }
              > div:first-child {
                font-size: 16px;
              }
              > div:nth-of-type(3) {
                font-size: 13px;
                color: #c8a060;
                margin-top: 10px;
              }
            }
            > div:last-child {
              height: 100%;
              text-align: right;
              > div:first-child {
                font-size: 13px;
                color: #000;
              }
              > div:last-child {
                font-size: 13px;
                margin-top: 2px;
                color: #666666;
              }
            }
          }
        }
        > div {
          display: flex;
          justify-content: flex-end;
          margin-top: 10px;
        }
        > div:nth-of-type(2) {
          font-size: 13px;
          > span:first-child {
            color: #666;
            margin-right: 10px;
          }
          > span:last-child {
            color: #000;
            > span {
              color: #7e3330;
            }
          }
        }
        > div:nth-of-type(3) {
          > span {
            margin-left: 15px;
            border: solid 1px #a0a0a0;
            color: #666666;
            padding: 4px 8px;
            // width: 74px;
            // height: 30px;
            // display: inline-flex;
            // align-items: center;
            // justify-content: center;
            font-size: 14px;
            border-radius: 25px;
          }
          > span:last-child {
            border: solid 1px #7e3330;
            color: #7e3330;
          }
        }
      }
    }
  }
}
</style>



